
import { mapGetters, mapMutations } from "vuex";
import XHeader from 'vux/src/components/x-header/index.vue'
import Panel from 'vux/src/components/panel/index.vue'

import scroll from "@/base/scroll/scroll.vue";
import { playlistDetail } from "@/api/api.js";
export default {
data() {
return {
playlistDetailData: {},
songList: []
};
},

beforeCreate() {},

created() {
this._playlistDetail();
},

beforeMount() {},

mounted() {},

beforeUpdate() {},

updated() {},

beforeDestroy() {},

destroyed() {},

methods: {
_playlistDetail() {
const id = this.$route.params.id;
this.$vux.loading.show({
text: "Loading"
});
playlistDetail(id).then(res => {
this.playlistDetailData = res.result;
this.songList = res.result.tracks.map(item => {
return {
title: item.name,
desc: item.artists[0].name,
src: item.album.picUrl,
id: item.id
};
});

this.$vux.loading.hide();
});
},
handleListClick(song) {

this.setPlayList(song);
},

...mapMutations({
setPlayList: "SET_PLAYLIST"
})
},
computed: {
bgStyle() {
return `background-image:url(${this.playlistDetailData.coverImgUrl})`;
},

...mapGetters(["playList"])
},
components: {
XHeader,
Panel,
scroll
}
};



